// Grid system
//
// Generate semantic grid columns with these mixins.

// Centered container element
.container-fixed(@gutter: var(--bs-grid-gutter-width)) {
  padding-right: calc(@gutter / 2);
  padding-left: calc(@gutter / 2);
  margin-right: auto;
  margin-left: auto;
  @supports (width: round(up, 1px, 1px)) {
    padding-right: ~"round(up, calc("@gutter~" / 2), 1px)";
    padding-left: ~"round(down, calc("@gutter~" / 2), 1px)";
  }
  &:extend(.clearfix all);
}

// Creates a wrapper for a series of columns
.make-row(@gutter: var(--bs-grid-gutter-width)) {
  margin-right: calc(@gutter / -2);
  margin-left: calc(@gutter / -2);
  @supports (width: round(up, 1px, 1px)) {
    margin-right: ~"round(down, calc("@gutter~" / -2), 1px)";
    margin-left: ~"round(up, calc("@gutter~" / -2), 1px)";
  }
  &:extend(.clearfix all);
}

// Generate the extra small columns
.make-xs-column(@columns; @gutter: var(--bs-grid-gutter-width)) {
  position: relative;
  float: left;
  width: percentage((@columns / @grid-columns));
  min-height: 1px;
  padding-right: calc(@gutter / 2);
  padding-left: calc(@gutter / 2);
}
.make-xs-column-offset(@columns) {
  margin-left: percentage((@columns / @grid-columns));
}
.make-xs-column-push(@columns) {
  left: percentage((@columns / @grid-columns));
}
.make-xs-column-pull(@columns) {
  right: percentage((@columns / @grid-columns));
}

// Generate the small columns
.make-sm-column(@columns; @gutter: var(--bs-grid-gutter-width)) {
  position: relative;
  min-height: 1px;
  padding-right: calc(@gutter / 2);
  padding-left: calc(@gutter / 2);

  @media (min-width: @screen-sm-min) {
    float: left;
    width: percentage((@columns / @grid-columns));
  }
}
.make-sm-column-offset(@columns) {
  @media (min-width: @screen-sm-min) {
    margin-left: percentage((@columns / @grid-columns));
  }
}
.make-sm-column-push(@columns) {
  @media (min-width: @screen-sm-min) {
    left: percentage((@columns / @grid-columns));
  }
}
.make-sm-column-pull(@columns) {
  @media (min-width: @screen-sm-min) {
    right: percentage((@columns / @grid-columns));
  }
}

