// Alerts

.alert-variant(@background; @border; @text-color) {
  color: @text-color;
  background-color: @background;
  border-color: @border;

  hr {
    border-top-color: @border;
    @supports (color: hsl(from #000 h s calc(l - 1))) {
      border-top-color: hsl(from @border h s calc(l - 5));// browsers supported ( https://caniuse.com/css-relative-colors )
    }
  }

  .alert-link {
    color: @text-color;
    @supports (color: hsl(from #000 h s calc(l - 1))) {
      color: hsl(from @text-color h s calc(l - 10));
    }
  }
}
